.wallet-header-text-row {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.wallet-header-text {
  font-size: 24px;
}
.wallet-header-icon-container {
  cursor: pointer;
}
.wallet-header-icon-container svg {
  height: 1.5em;
}
.wallet-header-balance-row {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 20px;
}
.wallet-header-dollar-icon-container {
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
}
.wallet-header-balance-container {
  margin-left: 20px;
}
.wallet-header-balance-text {
  font-size: 15px;
}
.wallet-header-balance-value {
  font-size: 30px;
  margin-top: 5px;
}
