.coinitem-container {
  margin-top: 20px;
  padding: 20px 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  border: 1px solid rgba(34, 38, 76, 0.1);
  background: #fff;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}
.coinitem-container:hover {
  background-color: #22264c;
  color: white;
}
.coinitem-container:hover .coinitem-text-value {
  color: rgba(255, 255, 255, 0.75);
}
.coinitem-container:hover .coinitem-text-value-currency {
  color: rgba(255, 255, 255, 0.51);
}
.coinitem-container:hover .coin-item-current-value {
  color: rgba(255, 255, 255, 0.75);
}
.coinitem-container:hover .coin-item-diff-text {
  color: rgba(255, 255, 255, 0.51);
}
.coinitem-container:hover .coinitem-chart-line {
  background-color: rgba(255, 255, 255, 0.1);
}
.coinitem-chart-container {
  position: relative;
}
.coinitem-chart-line {
  position: absolute;
  left: -30px;
  width: 2px;
  height: 41px;
  background-color: rgba(61, 63, 80, 0.08);
  top: 30px;
}
.coinitem-graph-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.coinitem-img {
  width: 50px;
}
.coinitem-stats-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.coinitem-text-title {
  font-size: 18px;
  font-weight: bold;
}
.coinitem-text-value {
  color: rgba(34, 38, 76, 1);
  margin-top: 10px;
}
.coinitem-text-value-currency {
  color: rgba(95, 93, 116, 1);
  margin-top: 10px;
}
.coinitem-text-container {
  margin-left: 10px;
}
.coin-item-graph-container {
  width: 100px;
}
.coin-item-current-value {
  color: rgba(34, 38, 76, 0.75);
  font-size: 13px;
  margin-top: 10px;
}
.coin-item-diff-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 14px;
  margin-top: 5px;
}
.coin-item-diff-text {
  margin-left: 5px;
  color: rgba(95, 93, 116, 0.7);
}

.coin-item-diff-value {
  color: #ea333e;
  font-weight: 700;
}
.coin-item-diff-value.isPositive {
  color: #6cbc3b;
}
