.transaction-item {
  margin-top: 20px;
  border-radius: 5px;
  border: 1px solid rgba(34, 38, 76, 0.1);
  background: #fff;
  padding: 10px;
  cursor: pointer;
}
.transaction-item-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.transaction-item-type-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.transaction-item-icon-container {
  border-radius: 50%;
  padding: 20px;
  background: rgba(34, 38, 76, 0.1);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.transaction-item-type-text {
  margin-left: 10px;
}
.transaction-item-type-value {
  color: #22264c;
  font-size: 18px;
}
.transaction-item-type-date {
  color: #22264c;
  font-size: 12px;
  margin-top: 5px;
}
.transaction-item-amount-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
}
.transaction-item-amount-value {
  color: #22264c;
  font-size: 16px;
  font-weight: 700;
}
.transaction-item-amount-currency {
  color: #22264c;
  font-size: 12px;
  opacity: 0.4;
  margin-top: 5px;
}
