.bank-item-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 20px 10px;
  border: 1px solid rgba(34, 38, 76, 0.1);
  border-radius: 5px;
  margin-top: 15px;
  cursor: pointer;
  user-select: none;
}
.bank-item-container:hover {
  border: 1px solid #005eff;
}
.bank-item-content {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.bank-item-text {
  margin-left: 10px;
}
.bank-item-number {
  color: #22264c;
  font-weight: bold;
  font-size: 18px;
}
.bank-item-name {
  margin-top: 5px;
}
.bank-item-content-icon-container svg {
  font-size: 40px;
  color: #005eff;
}
.bank-item-ischecked {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.bank-item-icon-container {
  display: none;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #005eff;
  color: white;
  padding: 5px;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  cursor: pointer;
}

.bank-item-icon-container.empty {
  display: flex;
  background-color: transparent;
  border: 1px solid rgba(34, 38, 76, 0.3);
}

.bank-item-container:hover .bank-item-ischecked .bank-item-icon-container {
  display: flex;
}

.bank-item-container:hover
  .bank-item-ischecked
  .bank-item-icon-container.empty {
  display: none;
}

.bank-item-container.isSelected .bank-item-ischecked .bank-item-icon-container {
  display: flex;
}

.bank-item-container.isSelected .bank-item-ischecked .bank-item-icon-container.empty {
  display: none;
}
