.input-container {
  position: relative;
  width: 100%;
}

.input {
  box-sizing: border-box;
  padding: 16px 10px;
  border: 1px solid whitesmoke;
  border-radius: 5px;
  width: 100%;
}
.input.hasIcon {
  padding-left: 40px;
}
.input.hasPassIcon {
  padding-right: 40px;
}
.input-icon-container {
  position: absolute;
  left: 15px;
  top: 16px;
  color: rgba(34, 38, 76, 0.65);
}
.input-type-container {
  position: absolute;
  right: 15px;
  top: 16px;
  cursor: pointer;
  color: #005eff;
}
