.auth-image-container.forgotpass {
  padding: 10px 20px;
  width: 100%;
}
.forgotpass-image {
  width: 285px;
}
.forgotpass-content-container {
  background-color: #f5f5fa;
  padding: 35px 20px 10px 20px;
  width: 100%;
  box-sizing: border-box;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}
.forgotpass-text-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.forgotpass-title {
  font-size: 24px;
  color: #22264c;
  font-weight: bold;
}
.forgotpass-desc {
  text-align: center;
  margin-top: 10px;
  line-height: 1.5;
}
