.coinwallet-body-container {
  padding: 10px 20px;
  width: 100%;
  box-sizing: border-box;
  font-weight: bold;
}
.InfoPrompt-header {
  color: #22264c;
  font-size: 20px;
  width: 100%;
  text-align: center;
}
.infoPrompt-amount {
  color: #005eff;
  font-size: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 10px 0px;
}
.infoPrompt-icon-container {
  margin-left: 10px;
  cursor: pointer;
}
.infoPrompt-desc {
  margin: 10px 0px;
  line-height: 1.5;
  color: #757575;
}
.infoPrompt-card {
  margin-top: 10px;
  border-radius: 15px;
  padding: 20px;
  width: 100%;
  box-sizing: border-box;
  box-shadow: 0px 22px 57px 0px rgba(34, 41, 72, 0.1);
}
.infoPrompt-text-header {
  padding-left: 10px;
  border-left: 4px solid #005eff;
}
.infoPrompt-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px;
}
.infoPrompt-name {
  color: #757575;
}
.infoPrompt-value {
  display: flex;
  flex-direction: row;
}
.infoPrompt-btn-row {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.infoPrompt-btn-item {
  margin-top: 10px;
  width: 100%;
}
