.tabbar-space {
  margin-top: 100px;
}
.tabbar-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: white;
  position: absolute;
  width: 100%;
  bottom: 0;
  box-sizing: border-box;
  z-index: 3;
}
.tab-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  position: relative;
}
.tab-title {
  margin-top: 5px;
}
.tab-item.selected {
  color: #f7931a;
}
.exchange-img-container {
  background: linear-gradient(90deg, #0771ee 0%, #0500f6 100%);
  filter: drop-shadow(0px 7px 12px rgba(0, 0, 0, 0.58));
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  position: absolute;
  bottom: 3px;
  padding: 15px;
  border-radius: 50%;
}
.exchange-img-container:hover {
  background: linear-gradient(0deg, #0500f6 90%, #006be6 100%);
}
.exchange-img {
  width: 30px;
}
