.button {
  width: 100%;
  background-color: #005eff;
  color: white;
  padding: 15px 0px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  border: 1px solid transparent;
}
.button:hover {
  background-color: transparent;
  color: #005eff;
  border: 1px solid #005eff;
}
button.green {
  border: 0px solid #22a079;
  background: #22a079;
}
button.green-alt {
  background-color: transparent;
  border: 1px solid #22a079;
  color: #22a079;
}
button.green-alt:hover {
  background-color: #22a079;
  border: 1px solid #22a079;
  color: white;
}
button.green:hover {
  background-color: transparent;
  color: #22a079;
  border: 1px solid #22a079;
}
button.yellow {
  background-color: #f7931a;
}
button.yellow:hover {
  background-color: transparent;
  color: #f7931a;
  border: 1px solid #f7931a;
}
button.yellow-alt {
  background-color: transparent;
  border: 1px solid #f7931a;
  color: #f7931a;
}
button.yellow-alt:hover {
  background-color: #f7931a;
  color: white;
  border: 1px solid #f7931a;
}

button.blue-alt {
  background-color: transparent;
  border: 1px solid #005eff;
  color: #005eff;
}
button.blue-alt:hover {
  background-color: #005eff;
  color: white;
  border: 1px solid #005eff;
}

.button.red {
  background: linear-gradient(to left, transparent 50%, #fb246a 50%);
  background-size: 200% 100%;
  background-position: right bottom;
  border: 1px solid #fb246a;
  color: #fb246a;
}
.red:hover {
  background-position: left bottom;
  color: white;
}
.grey {
  border: 0px solid #f1f1f4;
  background: #f1f1f4;
  color: #22264c;
}
