.exchange-body-container {
  width: 100%;
}
.exchange-accordian-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.exchange-accordian-content {
  width: 200px;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 20px 0px;
  border: 1px solid #005eff;
  color: #005eff;
  border-radius: 25px;
}
.exchange-accordian-item {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
  box-sizing: border-box;
  cursor: pointer;
  border-radius: 25px;
  user-select: none;
}
.exchange-accordian-item.selected {
  background-color: #005eff;
  color: white;
}
.exchange-inputs-container {
  width: 100%;
  box-sizing: border-box;
  padding: 0px 20px;
  display: flex;
  flex-direction: column;
}
.exchange-inputs-container.reverse {
  flex-direction: column-reverse;
}
.exchange-actions-container {
  width: 100%;
  box-sizing: border-box;
  padding: 0px 20px;
}
.exchange-input-item {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-top: 20px;
  z-index: 3;
}
.exchange-input {
  width: 100%;
}
.exchange-input-select {
  position: absolute;
  top: 0;
  z-index: 3;
  left: 0;
  height: 100%;
}
.exchange-input-select {
  z-index: 2;
}
.exchange-btn-container {
  margin-top: 20px;
}
.exchange-textbox {
  font-size: 30px;
}
.exchange-coin-option {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.exchange-coin-image {
  width: 20px;
  height: auto;
  margin-right: 5px;
}
.exchange-wallet-header {
  padding: 30px 20px;
}
.exchange-addbank-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 20px 10px;
  border: 1px dotted #005eff;
  box-sizing: border-box;
  width: 100%;
  border-radius: 5px;
  color: #22264c;
  cursor: pointer;
  user-select: none;
  margin-top: 20px;
  font-size: 18px;
}
.exchange-addbank-container span {
  margin-left: 10px;
}
