.pin-container {
  width: 100%;
  display: flex;
  position: relative;
}
.pin-content {
  width: 100%;
  box-sizing: border-box;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 2;
}
.pin-header {
  color: #005eff;
  font-weight: bold;
  font-size: 32px;
  margin-top: 10px;
}
.pin-desc {
  color: #005eff;
  font-size: 16px;
  margin-top: 10px;
}
.pin-digits-container {
  margin-top: 20px;
}
.pin-digits-row {
  margin-top: 20px;
}
.pin-inputs-container {
  margin-top: 10px;
}
.pin-btn-container {
  width: 100%;
  margin-top: 30px;
}
