.settings-container {
  width: 100%;
}
.settings-header {
  margin-top: 20px;
  text-align: center;
  color: #22264c;
  font-size: 20px;
  font-weight: 700;
}
.settings-list-container {
  padding: 0px 20px;
  margin-top: 30px;
}
.settings-list-item {
  margin-top: 20px;
}
.settings-list-item-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(36, 37, 43, 0.2);
}
.settings-list-item-header-text {
  margin-left: 10px;
  font-size: 19px;
  font-weight: bold;
}
.settings-list-item-value {
  margin-top: 10px;
  cursor: pointer;
}
.settings-list-item-value:hover {
    color: #0771ee;
}