.auth-image-container.forgotpass {
  padding: 10px 20px;
  width: 100%;
}
.email-image {
  width: 200px;
  margin: 30px 0px;
}
.email-content-container {
  background-color: #f5f5fa;
  padding: 35px 20px 10px 20px;
  width: 100%;
  box-sizing: border-box;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}
.email-text-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.email-title {
  font-size: 24px;
  color: #22264c;
  font-weight: bold;
}
.email-desc {
  text-align: center;
  margin-top: 10px;
  line-height: 1.5;
}

.digit-input-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.resend-text {
  width: 100%;
  text-align: center;
  margin: 20px 0px;
  color: rgba(34, 38, 76, 0.7);
}
.resend-text span {
  color: #005eff;
  cursor: pointer;
}
