.editprofile-form {
  margin: 0px 20px;
}
.editprofile-title {
  color: #22264c;
  padding-top: 25px;
  font-weight: bold;
}
.profile-image-edit-icon-container {
  position: absolute;
  top: 60%;
  background-color: #2b56f5;
  border: 1px solid white;
  color: white;
  padding: 10px;
  border-radius: 50%;
  right: 0;
  cursor: pointer;
}
.profile-avatar-item{
  padding: 5px;
  border-radius: 50%;
  border: 1px solid transparent;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.profile-avatar-item.selected{
  border: 1px solid #2b56f5;
}

.profile-avatar-list {
  width: 100%;
  box-sizing: border-box;
  padding: 0px 20px;
  display: flex;
  flex-direction: column;
}
.profile-avatar-row {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  margin: 10px 0px;
}
.profile-avatar-item-img {
  width: 50px;
  height: auto;
}
