.transfer-status {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
    height: 100%;
}
.transfer-status-icon-container {
  background-color: rgba(0, 94, 255, 0.1);
  color: #22a079;
  padding: 30px;
  border-radius: 50%;
  /* margin-top: 50px; */
}
.transfer-status-icon-container svg {
  height: 3rem;
}
.transfer-status-header {
  margin-top: 20px;
  color: #22264c;
  text-align: center;
  font-size: 24px;
  font-weight: 500;
}
.transfer-status-desc {
  margin-top: 10px;
  color: #22264c;
  text-align: center;
  font-size: 16px;
  font-weight: 400;
}
.transfer-status-btn-container {
  margin-top: 10px;
  width: 100%;
  padding: 0px 20px;
  box-sizing: border-box;
  margin-top: 50px;
}
.transfer-status-btn {
  padding: 10px;
  border-radius: 8px;
  text-align: center;
  background: rgba(0, 94, 255, 0.1);
  box-sizing: border-box;
  width: 100%;
  cursor: pointer;
  user-select: none;
}
