.coin-wallet-header {
  padding-top: 30px;
}
.coinwallet-header-row {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.coinwallet-balance {
  width: 100%;
  text-align: center;
  margin-top: 20px;
  font-size: 35px;
  font-weight: bold;
}
.coinwallet-coinprice-dropdown {
  min-width: 170px;
}
.coinwallet-balance-currency {
  width: 100%;
  text-align: center;
  font-size: 16px;
  margin-top: 10px;
}
.coinwallet-header-btn-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}
.coinwallet-header-btn {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 22px;
  cursor: pointer;
}
.coinwallet-header-btn.green {
  background: rgba(78, 179, 148, 1);
}
.coinwallet-header-btn.green:hover {
  background: rgba(78, 179, 148, 0.8);
}
.coinwallet-header-btn.yellow {
  background: rgba(249, 178, 92, 1);
}
.coinwallet-header-btn.yellow:hover {
  background: rgba(249, 178, 92, 0.8);
}
.coinwallet-header-btn.blue {
  background: rgba(51, 126, 255, 1);
}
.coinwallet-header-btn.blue:hover {
  background: rgba(51, 126, 255, 0.8);
}
.coinwallet-header-btn-text {
  margin-left: 10px;
}
.coinwallet-body-container {
  background-color: white;
  position: relative;
  z-index: 3;
  margin-top: -30px;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
}
.coinwallet-coinprice-row {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 20px 20px 0px 20px;
}
.coinwallet-coinprice-value {
  color: #22264c;
  font-size: 16px;
  font-weight: 400;
}
.coinwallet-coinprice-text-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
}
.coinwallet-coinprice-variation {
  color: #ea333e;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  text-align: right;
  padding: 0px 20px;
  margin-bottom: 10px;
}
.coinwallet-coinprice-variation.isPositive {
  color: #6cbc3b;
}

.coinwallet-chart-container {
  width: 100%;
}
.coinwallet-transactions-list {
  padding-bottom: 30px;
}
.coinwallet-transactions-container {
  padding: 0px 20px;
  margin-top: 20px;
  color: #22264c;
  font-size: 18px;
}
.coinwallet-pagination-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.coinwallet-pagination-container.green .pagination-item.selected {
  background-color: #22a079;
  color: white;
}
.coinwallet-pagination-container.green .pagination-item {
  color: #22a079;
}

.coinwallet-pagination-container.blue .pagination-item.selected {
  background-color: #005eff;
  color: white;
}
.coinwallet-pagination-container.blue .pagination-item {
  color: #005eff;
}

.coinwallet-pagination-container.yellow .pagination-item.selected {
  background-color: #f7931a;
  color: white;
}
.coinwallet-pagination-container.yellow .pagination-item {
  color: #f7931a;
}
