@font-face {
  font-family: "ComicNeue";
  src: url(./fonts/ComicNeue.ttf) format("truetype");
}

#root,
body {
  margin: 0;
  padding: 0;
  font-family: ComicNeue, sans-serif;
  font-size: 16px;
  width: 400px;
  height: 600px;
  overflow-x: hidden;
}

.auth-label {
  font-size: 16px;
  margin-bottom: 5px;
}
.auth-form-item {
  margin-top: 10px;
}
.auth-image-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.auth-content-container {
  margin-top: 20px;
}
.auth-form-link {
  font-size: 14px;
  color: #22264c;
  font-family: sans-serif;
  margin: 10px 0px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
.auth-form-link:hover {
  cursor: pointer;
  color: #005eff;
}
.auth-alternate-link {
  font-size: 16px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.container {
  position: relative;
  box-sizing: border-box;
  padding: 10px 20px;
  width: 100%;
}
.auth-alternate-link span {
  color: #f7931a;
  padding: 0px 5px;
  cursor: pointer;
}

.wallet-header-container {
  background-color: #003687;
  color: white;
  padding: 50px 20px;
  position: relative;
}
.wallet-header-content {
  position: relative;
  z-index: 2;
}
.wallet-header-container.yellow {
  background-color: #f7931a;
}
.wallet-header-container.blue {
  background-color: #003687;
}
.wallet-header-container.light-blue {
  background-color: #005eff;
}
.wallet-header-container.green {
  background-color: #22a079;
}

.wallet-coins-container {
  padding: 10px 20px;
}

.wallet-header-circle {
  position: absolute;
  top: 0;
  left: 26%;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
}
.lightblue-circle {
  background-image: url(./assets/lightblueCircle.png);
}
.blue-circle {
  background-image: url(./assets/blueCircle.png);
}
.green-circle {
  background-image: url(./assets/greenCircle.png);
}
.yellow-circle {
  background-image: url(./assets/yellowCircle.png);
}
.transparent-circle {
  position: absolute;
  top: 0;
  right: 0;
  background-image: url(./assets/transparentCircle.png);
  width: 100%;
  left: 40%;
  display: flex;
  height: 100%;
  background-repeat: no-repeat;
  z-index: 1;
}
/* wave */
.wave-container {
  position: relative;
  width: 100%;
  height: 50vh;
  background-color: #3498db; /* Background color of the wave */
}

.wave {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px; /* Height of the wave */
  background: url("https://cdn.pixabay.com/photo/2016/07/01/12/58/background-1497231_960_720.jpg"); /* You can change this to an image if you want */
  background-size: 100% 100%;
  animation: wave-animation 2s infinite linear;
}

@keyframes wave-animation {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(
      -25px
    ); /* Adjust this value to control the amplitude of the wave */
  }
}
