.homeWalletItem-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  background-color: #f8f8f8;
  width: 100%;
  box-sizing: border-box;
  padding: 20px 10px;
  border-radius: 10px;
}
.homeWalletItem-img {
  width: 50px;
}
.homeWalletItem-coins-container {
  margin-left: 5px;
}
.homeWalletItem-coins-value {
  color: #8e8e8e;
  margin-top: 5px;
}
.homeWalletItem-price-container {
  margin-left: 5px;
}
.homeWalletItem-price-variation {
  margin-top: 5px;
  color: #8e8e8e;
}
.homeWalletItem-price-percentage {
  color: #ea333e;
  font-weight: 700;
}
.homeWalletItem-price-percentage.isPositive {
  color: #6cbc3b;
}
