.refer-image-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.refer-title {
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  width: 100%;
  font-size: 24px;
  color: #22264c;
}
.refer-desc {
  text-align: center;
  color: rgba(34, 38, 76, 0.7);
  margin-top: 10px;
}
.refer-stats-container {
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border: 1px dashed #c7c7c7;
  padding: 10px;
  border-radius: 10px;
}
.refer-stats-pending-container {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.refer-stats-pending-title {
  font-size: 22px;
}
.refer-stats-pending-desc {
  margin-top: 10px;
  color: rgba(34, 38, 76, 0.7);
}
.refer-stats-wins-container {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.refer-stats-wins-title {
  font-size: 22px;
  color: #005eff;
}
.refer-stats-wins-desc {
  margin-top: 10px;
  color: rgba(34, 38, 76, 0.7);
}
.refer-link-title {
  font-size: 14px;
  color: rgba(34, 38, 76, 0.7);
  margin-top: 10px;
}
.refer-link-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  border: 1px solid rgba(34, 38, 76, 0.16);
  box-sizing: border-box;
  margin-top: 10px;
  border-radius: 5px;
}
.refer-link-copy-container {
  cursor: pointer;
}
.refer-btn-container {
  margin-top: 10px;
}
.referral-header-text {
  margin-left: 93px;
}
