.preference-header-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.preference-icon-container {
  font-size: 24px;
  cursor: pointer;
}
.preference-header-text {
  margin-left: 120px;
  font-size: 24px;
}
.preference-content-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 20px;
  box-sizing: border-box;
}
.preference-btn-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
  margin-top: 20px;
}
.preference-btn-item{
    width: 45%;
}