.line-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-self: center;
}
.line {
  margin-top: 10px;
  width: 50px;
  height: 5px;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-self: center;
  content: "";
  border-radius: 20px;
  background: rgba(34, 38, 76, 0.21);
}
.qr-text-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-self: center;
  margin-top: 20px;
}
.qr-title {
  color: #24252b;
  text-align: center;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}
.qr-desc {
  color: rgba(36, 37, 43, 0.7);
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  margin-top: 10px;
}
.qr-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-self: center;
  margin-top: 20px;
}
.recieve-wallet-address-text {
  color: #24252b;
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  margin-top: 20px;
}
.recieve-wallet-address-value {
  text-align: center;
  color: rgba(36, 37, 43, 1);
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  margin-top: 10px;
}
.recieve-wallet-btn-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 30px 20px;
}
.recieve-wallet-btn {
  border-radius: 8px;
  text-align: center;
  padding: 15px 38px;
  user-select: none;
  cursor: pointer;
}
.recieve-wallet-btn.green {
  background-color: rgba(34, 160, 121, 0.1);
  color: rgba(34, 160, 121, 1);
}
.recieve-wallet-btn.yellow {
  background-color: rgba(247, 147, 26, 0.1);
  color: rgba(247, 147, 26, 1);
}
.recieve-wallet-btn.blue {
  background-color: rgba(0, 94, 255, 0.1);
  color: rgba(0, 94, 255, 1);
}
