.home-header-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #005eff;
  color: white;
  width: 100%;
  box-sizing: border-box;
  padding: 0px 20px 100px 20px;
}
.home-icon-container {
  width: 100%;
  text-align: right;
  margin-top: 20px;
  font-size: 20px;
  cursor: pointer;
}
.home-info-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.home-info-image {
  width: 50px;
}
.home-info-text-container {
  margin-left: 10px;
}
.home-info-name {
  font-weight: bold;
}
.home-info-balance {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 5px;
}
.home-info-balance-container {
  margin-left: 10px;
  cursor: pointer;
}
.home-info-balance-text {
  font-weight: bold;
  margin-right: 5px;
}
.home-pages-container {
  width: 75%;
}
.home-pages-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 20px;
}
.home-pages-item {
  background-color: rgba(223, 222, 224, 0.12);
  padding: 10px 15px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 45px;
  cursor: pointer;
}
.home-pages-item-text {
  margin-top: 5px;
}
.home-header-img {
  position: absolute;
  bottom: 0px;
  width: 100%;
}
.home-header-background {
  position: relative;
}
.home-wallet-text {
  padding: 0px 20px;
  font-weight: bold;
}
.home-referal-container {
  margin: 0px 20px;
  background-color: #002051;
  color: white;
  padding: 20px;
  border-radius: 10px;
  cursor: pointer;
}
.home-referal-item-btn {
  background-color: white;
  color: #ea9633;
  width: max-content;
  padding: 10px;
  border-radius: 5px;
  margin-top: 10px;
}
.home-referal-item-text.bold {
  font-weight: bolder;
}
