.review-transaction-header {
  padding: 20px;
}
.review-transaction-row {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.review-transaction-text {
  color: #fff;
  font-size: 20px;
  font-weight: 600;
}
.review-transaction-icon-container {
  cursor: pointer;
}
.review-transaction-icon-container svg {
  height: 1.5rem;
}
.review-transaction-text {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 10px;
}
.review-transaction-image {
  width: 50px;
  height: auto;
}
.review-transaction-value {
  font-size: 30px;
  margin-left: 10px;
}
.review-transaction-value span {
  color: rgba(255, 255, 255, 0.7);
}
.review-transaction-body-container {
  margin-top: 20px;
  padding: 0px 20px;
  width: 100%;
  box-sizing: border-box;
}
.review-transaction-item {
  border-bottom: 1px solid rgba(34, 38, 76, 0.2);
  padding-bottom: 10px;
  margin-top: 20px;
}
.review-transaction-body-container .review-transaction-item:last-child {
  border-bottom: 0px;
}
.review-transaction-item-header {
  color: rgba(34, 38, 76, 0.8);
  font-size: 13px;
  font-weight: 500;
}
.reveiw-transcation-item-value {
  color: #22264c;
  font-size: 14px;
  font-weight: 600;
  margin-top: 5px;
}
.review-btn-container {
  width: 100%;
  box-sizing: border-box;
  padding: 20px 20px;
}
