.change-password-header {
  width: 100%;
  text-align: center;
  color: #22264c;
  margin-top: 30px;
  font-size: 24px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.change-password-back-icon {
  font-size: 24px;
  cursor: pointer;
}
.change-password {
  position: relative;
  width: 100%;
}
.change-password-content {
  width: 100%;
  box-sizing: border-box;
  padding: 0px 20px;
}
.change-password-btn-container {
  margin-top: 20px;
}
.change-password-inputs-container {
  margin-top: 60px;
}
.change-password-text{
    margin-left: 85px;
}