.main-container {
  margin: 20px;
}
.main-content {
  background-color: #f5f5fa;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border-radius: 25px;
}
.main-content-img{
    width: auto;
    height: 300px;
}
.main-content-title {
  font-size: 32px;
  margin-bottom: 20px;
}
.main-desc {
  text-align: center;
  font-size: 20px;
  margin-top: 10px;
}
.main-btn-container {
  margin-top: 10px;
}
.main-btn {
  margin-top: 10px;
}
