.profile-container {
  background: #f8f8fb;
  position: relative;
}
.profile-content {
  z-index: 2;
  position: relative;
}

.profile-title {
  padding: 20px 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  font-weight: bolder;
}
.profile-image-container {
  position: relative;
}
.profile-image {
  border: 3px solid #6ba2fd;
  border-radius: 50%;
  padding: 5px;
}
.profile-image-active {
  background-color: #03b567;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 4px solid white;
  position: absolute;
  top: 34%;
  right: -4%;
}
.profile-text {
  font-size: 24px;
}
.profile-header {
  margin-top: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 40px;
}
.profile-name {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.profile-name-text {
  color: #22264c;
  font-size: 26px;
}
.profile-name-icon-container {
  background-color: #005eff;
  color: white;
  margin-left: 5px;
  padding: 0.2rem;
  border-radius: 50%;
  display: flex;
}
.profile-name-icon-container svg {
  height: 0.5rem;
}
.profile-username {
  color: rgba(34, 38, 76, 0.5);
}
.profile-body {
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  background-color: white;
}
.profile-body-title {
  padding: 20px 0px;
  text-align: center;
  width: 100%;
  color: #005eff;
  font-size: 20px;
  border-bottom: 1px solid rgba(34, 38, 76, 0.1);
}
.profile-detail-list {
  padding: 0px 20px;
}
.profile-detail-item {
  margin-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(34, 38, 76, 0.1);
}
.profile-detail-header {
  color: rgba(34, 38, 76, 0.5);
  font-size: 10px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-transform: uppercase;
}
.profile-detail-value {
  margin-top: 10px;
}
.profile-body-container {
  padding: 20px;
}
